<template>
  <div>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="状态：">
            <el-select v-model="searchList.withdrawType" clearable>
              <el-option label="全部" value="0"></el-option>
              <el-option label="审核中" value="1"></el-option>
              <el-option label="处理中" value="2"></el-option>
              <el-option label="提现成功" value="3"></el-option>
              <el-option label="已驳回" value="4"></el-option>
              <el-option label="提现失败" value="5"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-start" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>提现记录</span>
        </div>
        <div class="wordInfo flex-row-start">
          <div class="word warningFont">已成功提现{{sumWithdrawAmount}}元</div>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" tooltip-effect="dark" style="width: 100%">
          <el-table-column label="申请单号" width="300" prop="withdrawNo"></el-table-column>
          <el-table-column label="流水号" width="360" prop="paymentNo"></el-table-column>
          <el-table-column label="收支类型" width="160">
            <template slot-scope="scope"><span :data-it="scope.row.id">支出</span></template>
          </el-table-column>
          <el-table-column label="申请日期" width="200" prop="createTime"></el-table-column>
          <el-table-column label="提现金额" width="160" prop="withdrawAmount"></el-table-column>
          <el-table-column label="提现银行" width="200" prop="openName"></el-table-column>
          <el-table-column label="状态" width="160">
            <template slot-scope="scope">
              <div class="warningFont" v-if="scope.row.withdrawType == 1">审核中</div>
              <div class="warningFont" v-if="scope.row.withdrawType == 2">处理中</div>
              <div class="succeseFont" v-if="scope.row.withdrawType == 3">提现成功</div>
              <div class="dangerFont" v-if="scope.row.withdrawType == 4">提现失败</div>
              <div class="dangerFont" v-if="scope.row.withdrawType == 5">已驳回</div>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="$router.push({path:'/assetsManage/account/cashoutDetail',query:{id:scope.row.id}})">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopWithdrawList } from '@/api/assetsManage/account'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了提现记录')
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
        time:[],
        withdrawType:''
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0,
      sumWithdrawAmount:''
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      console.log('请求的数据',this.searchList)
      this.searchList.startDate = this.searchList.time[0] || ''
      this.searchList.endDate = this.searchList.time[1] || ''
      getShopWithdrawList(this.searchList).then(res=>{
        console.log('提现记录分页',res)
        if(res.code == 200){
          this.sumWithdrawAmount = res.data.sumWithdrawAmount
          this.list = res.data.page.list
          this.listTotal = res.data.page.total
        }
      })
    },
    reset(){
      this.searchList.withdrawType = ''
      this.searchList.time = []
      this.getData()
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .wordInfo{
    margin-left: 80px;
    .word{
      margin-right: 40px;
      font-size: 16px;
      font-weight: bold;
    }
  }
</style>